@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;0,900;1,600&display=swap");
body {
  font-family: "Poppins", sans-serif;

  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
 -webkit-tap-highlight-color: transparent;
}


.main-text{
  font-size: 1.5rem;
}

@media(min-width:375px){
 
.main-text{
  font-size: 1.7rem;
}
}

@media (min-width: 425px) {
  .main-text{
    font-size:2rem;
  }
}

@media (min-width: 615px) {
  .main-text{
    font-size:3rem;
  }
}

@media (min-width: 950px) {
  .main-text{
    font-size:4rem;
  }
}

@media (min-width: 1020px) {
  .main-text{
    font-size:3.7rem;
  }
}

@media (min-width: 1376px) {
  .main-text{
    font-size:5rem;
  }
}



.text-linear {
  background: -webkit-linear-gradient(#9499ff, #767ce8);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.btn {
  color: #9499ff;
  outline: 2px solid #9499ff;
  padding: 0.9rem 2.3rem;
  border-radius: 8px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  transition: all 500ms ease-in-out;
}

.btn:hover {
  background-color: #595eb0;
  background-position: left center; /* change the direction of the change here */
  outline: none;
  color: #e3e3e3;
}

@media (min-width: 600px) {
  .main {
    font-size: 3.5rem;
  }
}

@media (min-width: 1100px) {
  .main {
    font-size: 4.9rem;
  }
}

.project-title{
  letter-spacing:.5px;
}
.project-title:hover {
  background-image: linear-gradient(
    to right,
    #9499ff,
    #767ce8 50%,
    #1e1e1e 50%
  );
  background-size: 200% 100%;
  background-position: -100%;
  background-clip: text;
  -webkit-background-clip: text;
 
  -webkit-text-fill-color: transparent;
}

.project-title:before {
  content: "";
  background: #7b7ecc;
  display: block;
  transition: all 0.3s ease-in-out;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
}

.project-title:hover {
  background-position: 0;
}

.project-title:hover::before {
  width: 100%;
}

.project-title {
  transition: all 0.1s ease-in-out;
}

.project-img {
  transition: all 0.4s ease-in-out;
  filter: brightness(85%);
}

.project-img:hover {
  filter: brightness(40%);
  transform: scale(1.05);
}

.preview-link {
  box-shadow: inset 0 0 0 0 #51549c;
  color: #9499ff;
  margin: 0 -0.25rem;
  padding: 0 0.25rem;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}
.preview-link:hover {
  box-shadow: inset 300px 0 0 0 #535590;
  color: #e3e3e3;
}

.modal-scrollbar {
  overflow-y: scroll;
}

.modal-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scroll::-webkit-scrollbar {
  display: none;
}

.ahover{
  transition: all 400ms ease-in-out;
}

.ahover:hover {
  color: #9499ff !important;
}


@media(min-width:440px){
  .modal{
    top:20%;
  }
}

@media(min-width:550px){
  .modal{
    top: 25%;
    width: 400px;
  }
}

